<template lang="html">
  <div class="search-tab clearfix">
    <div class="search-tab-item" :class="{'active': sign === 'left'}" @click="tabHandle('left')" :style="{padding: padding}">
      <span class="left">{{leftText}}</span>
    </div>
    <div class="search-tab-item" :class="{'active': sign === 'right'}" @click="tabHandle('right')" :style="{padding: padding}">
      <span class="right">{{rightText}}</span>
    </div>

  </div>
</template>

<script>
export default {
  props: {
    padding: {
      type: String,
      default: '.28rem 0'
    },
    leftText: {
      type: String,
      default: '找企业'
    },
    rightText: {
      type: String,
      default: '找个人'
    }
  },
  data () {
    return {
      sign: 'left'
    }
  },
  methods: {
    tabHandle (sign) {
      this.sign = sign
      this.$emit('tab-handle', sign)
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../assets/css/variable.less';
@import '../../assets/css/property.less';
.search-tab {
  width: 100%;
  display: flex;
  background: @whiteColor;
  .search-tab-item {
    flex: 1;
    font-size: .32rem;
    text-align: center;
    // border-bottom: 1px solid @lineColor;
    &.active {
      color: @whiteColor;
      background: @activeColor;
    }
  }
}
</style>



