<template lang="html">
  <div class="content-title-box">
    <div class="content-title" :style="{margin: margin, marginTop: margint, padding: padding}" :class="{'borderB': true}">
      <p class="content-title__text content-title__text--left" :style="{color: leftcolor}">
        <img class="leftimg" v-if="leftimg" :src="leftimg" alt=""><span>{{lefttext}}</span></p>
      <p class="content-title__text content-title__text--right" :style="{color: rightcolor}">
        <img class="rightimg rightimg--right" :src="sorts[index]" alt="" @click.stop="imghandle(index)"><span>{{righttext}}</span></p>
    </div>
  </div>
</template>

<script>
import sortup from '@/assets/img/angle/arrw01.png'
import sortdown from '@/assets/img/angle/arrw02.png'
export default {
  props: {
    lefttext: {
      type: String,
      default: ''
    },
    righttext: {
      type: String,
      default: ''
    },
    isshow: {
      type: Boolean,
      default: false
    },
    leftimg: {
      type: String,
      default: ''
    },
    margint: {
      type: String,
      default: '0rem'
    },
    padding: {
      type: String,
      default: '0 .22rem 0 0'
    },
    margin: {
      type: String,
      default: '0'
    },
    borderb: {
      type: String,
      default: 'none'
    },
    bordert: {
      type: String,
      default: 'none'
    },
    leftcolor: {
      type: String,
      default: '#333'
    },
    rightcolor: {
      type: String,
      default: '#333'
    }
  },
  data () {
    return {
      index: 0,
      sorts: [sortup, sortdown]
    }
  },
  methods: {
    imghandle (index) {
      this.index = (index + 1) % 2
      this.$emit('img-handle')
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../assets/css/variable.less';
.content-title-box {
  width: 100%;
  height: .88rem;
  background: @whiteColor;
}
.content-title {

  position: relative;
  overflow: hidden;
}
.content-title .leftimg {
  width: .3rem;
  height: .3rem;
  position: relative;
  top: .05rem;
  margin-right: .2rem;
}
.content-title .rightimg {
  width: .44rem;
  height: .44rem;
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  margin-left: .2rem;
}
.content-title__text {
  font-size: .3rem;
  height: .88rem;
  line-height: .88rem;
  color: @defaultColor;
  background-color: @whiteColor;
}
.content-title__text--left {
  float: left
}
.rightimg--right,.content-title__text--right {
  float: right
}
</style>



