<template lang="html">
  <div class="coin-manage-page">
    <!-- 头部 -->
    <default-head header="广告费明细"></default-head>

    <div class="coin-box">
      <count-item class="borderB" @click.native="$router.push('/coindetail')" textLeft="我的广告费余额" textRight="">
        <span class="middle wrongColor">{{this.userInfo.adFeeCount}}币</span>
      </count-item>
    </div>

    <search-tab leftText="日均消费" rightText="充值明细" padding=".44rem 0" @tab-handle="changeTab"></search-tab>
    <white-title :lefttext="this.lefttext" righttext="排序" padding="0 .22rem" margint=".3rem"></white-title>
    <note-list v-for="(item, index) in data" :key="index" :time="item.time" :text="item.text"></note-list>
  </div>
</template>

<script>
import defaultHead from '@/components/head/defaultHead'
import countItem from '@/components/personcenter/countItem'
import searchTab from '@/components/search/searchTab'
import whiteTitle from '@/components/angle/whiteTitle'
import noteList from '@/components/angle/noteList'

export default {
  data () {
    return {
      lefttext: '消费记录',
      data: [],
      xiaofei: [
        {
          time: '2017-04-21 03:20:08',
          text: '200召币兑换¥2广告费'
        },
        {
          time: '2017-05-21 03:20:08',
          text: '2000召币兑换¥20广告费'
        },
        {
          time: '2017-06-21 03:20:08',
          text: '20000召币兑换¥200广告费'
        }
      ],
      chongzhi: [
        {
          time: '2017-06-21 04:20:08',
          text: '充值200召币'
        },
        {
          time: '2017-07-21 05:20:08',
          text: '充值2000召币'
        },
        {
          time: '2017-08-21 06:20:08',
          text: '充值20000召币'
        }
      ]
    }
  },
  computed: {
    userInfo: function () {
      return JSON.parse(sessionStorage.getItem('userInfo'))
    }
  },
  mounted () {
    this.data = this.xiaofei
    // console.log(JSON.parse(sessionStorage.getItem('userInfo')))
  },
  methods: {
    changeTab (sign) {
      if (sign === 'left') {
        this.lefttext = '消费记录'
        this.data = this.xiaofei
      } else if (sign === 'right') {
        this.lefttext = '充值记录'
        this.data = this.chongzhi
      }
    }
  },
  components: { defaultHead, countItem, searchTab, whiteTitle, noteList }
}
</script>

<style lang="less" scoped>
@import '../assets/css/variable.less';

.coin-manage-page {
  padding-top: .88rem;
  .coin-box {
    .middle {
      font-size: .36rem;
      float: left;
      margin-left: .8rem;
    }
  }
}

</style>



