
<template lang="html">
  <div class="note-list">
      <div class="note-list-item borderB">
          <div class="note-list-select fl" v-if="hasselect">
            <slot></slot>
          </div>
          <div class="note-list-left fl">
              <span class="time">{{time}}</span>
              <span class="content">{{text}}</span>
          </div>
          <div class="note-list-right fr" v-if="hassort">第<span class="topNum">{{topnum}}</span>名</div>
      </div>
  </div>
</template>

<script>
export default {
  props: {
    topnum: String | Number,
    time: String,
    text: String,
    hasselect: {
      type: Boolean,
      default: false
    },
    hassort: {
      type: Boolean,
      default: false
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../assets/css/variable.less';
.note-list {
  padding-left: .22rem;
  background: @whiteColor;
}
.note-list:last-child .note-list-item {
  border: none;
}
.note-list .note-list-item {
  width: 100%;
  display: block;
  height: 1.4rem;
  overflow: hidden;
  font-size: .3rem;
}
.note-list-item .note-list-select {
  width: .52rem;
  height: 1.4rem;
  padding-right: .22rem;
  position: relative;
  /*background: tan;*/
}
.note-list-item .note-list-left {
  width: 4.7rem;
  height: 1.4rem;
  background: @whiteColor;
}
.note-list-left span {
  display: block;
  margin-top: .12rem;
}
.note-list-left .time {
  height: .54rem;
  line-height: .54rem;
  font-size: .32rem;
  color: @midleColor;
}
.note-list-left .content {
  color: @defaultColor;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.note-list-item .note-list-right {
  width: 2rem;
  height: 1.4rem;
  line-height: 1.4rem;
  color: @midleColor;
  padding-right: .22rem;
  /*background: pink;*/
}
.note-list-right .topNum {
  color: @angleBlueFont;
}
</style>



